<template>
    <div :config="config" class="avatar-handler" :style="{width: displayWidth, 'min-height': displayHeight}">
        <ul class="nav nav-tabs" role="tablist">
            <li role="presentation" :class="currentIs('upload') ? 'active' : ''"><a href="#tabupload" aria-controls="tabupload" role="tab" data-toggle="tab">Upload</a></li>
            <li role="presentation" :class="currentIs('gravatar') ? 'active' : ''"><a href="#tabgravatar" aria-controls="tabgravatar" role="tab" data-toggle="tab">Gravatar</a></li>
            <li role="presentation" :class="currentIs('builtin') ? 'active' : ''"><a href="#tabbuiltin" aria-controls="builtin" role="tab" data-toggle="tab">Asmodee.net</a></li>
        </ul>
        <div class="tab-content">
            <div role="tabpanel" :class="'tab-pane' + (currentIs('upload') ? ' active' : '')" id="tabupload">
                <avatar-file-upload :config="{width: '100%', height: '100%', trans: config.trans, awsInputs: config.awsInputs, awsUrl: config.awsUrl}"></avatar-file-upload>
            </div>
            <div role="tabpanel" :class="'tab-pane' + (currentIs('gravatar') ? ' active' : '')" id="tabgravatar">
                <avatar-gravatar :config="{width: '100%', height: '100%', trans: config.trans}"></avatar-gravatar>
            </div>
            <div role="tabpanel" :class="'tab-pane' + (currentIs('builtin') ? ' active' : '')" id="tabbuiltin">
                <avatar-builtin-selector :config="{width: '100%', height: '100%', currentFilter: config.currentFilter, trans: config.trans}"></avatar-builtin-selector>
            </div>
        </div>
    </div>
</template>

<script>
import AvatarBuiltinSelector from '@/components/Profile/Avatar/AvatarBuiltinSelector';
import AvatarFileUpload from '@/components/Profile/Avatar/AvatarFileUpload';
import AvatarGravatar from '@/components/Profile/Avatar/AvatarGravatar';
import GenericMixin from '@mixins/generic';
import AvatarMixin from '@mixins/avatar';

export default {
    name: 'avatar_handler',

    mixins: [GenericMixin, AvatarMixin],

    components: {
        'avatar-builtin-selector': AvatarBuiltinSelector,
        'avatar-file-upload': AvatarFileUpload,
        'avatar-gravatar': AvatarGravatar
    },

    computed: {
        displayWidth () {
            return (this.config.width ? this.config.width : '100%');
        },

        displayHeight () {
            return (this.config.height ? this.config.height : '420px');
        },
    },

    data () {
        return {
            currentTab: this.config.initialTab
        };
    }
};
</script>

<style lang="scss" scoped>
@import '~sass/_variables.scss';
.avatar-handler /deep/ {
    h1 {
        color: #b3b3b3;
        font-size: 36px;
        font-weight: 400;
        display: block;
        text-align: center;
    }

    .nav.nav-tabs li {
        a {
            background: #fff;
            // border: 1px solid #b3b3b3;
            color: map-get($asnet-colors, 'dark-purple');
        }

        &.active, &:hover {
            a {
                background: linear-gradient(180deg, map-get($asnet-colors, 'dark-purple'), #2d0235);
                color: #f2f2f2;
                // background-color: #b3b3b3;
            }
        }
    }

    .form-base {
        margin: 0 auto;
        max-width: 360px;
        padding: 15px;
    }

    .kv-avatar {
        .file-preview {
            border: 1px solid #ddd;
            border-radius: 5px;
            margin-bottom: 5px;
            padding: 5px;
            width: 100%;

            .file-default-preview {
                background: #fff;
            }
        }
    }
}
</style>



