<template>
    <div :config="config" class="avatar-file-upload" :style="{width: displayWidth, 'min-height': displayHeight}">
        <h1 class="text-center item-title">{{ title }}</h1>
        <div id="kv-avatar-errors-1" v-if="fileErrors.length > 1" class="center-block alert alert-block alert-danger" style="width:800px;">
            <ul>
                <li v-for="(error, index) in fileErrors" :key="index" v-html="error"></li>
            </ul>
        </div>
        <form id="avatar-upload" class="form-base text-center" :action="config.awsUrl" method="POST" enctype="multipart/form-data">
            <input type="hidden" name="key" :value="'avatars/' + user.user_id" v-if="user" />
            <input v-for="(v, k) in awsInputs" type="hidden" :ref="'aws-input-' + k.toLowerCase()" :name="k" :value="v" :key="v" />

            <div class="kv-avatar center-block" style="width:200px;">
                <div class="file-input theme-fa has-error">
                    <button type="submit" tabindex="500" title="Upload selected files" class="btn btn-default fileinput-upload fileinput-upload-button" :disabled="isDisabled"><font-awesome-icon icon="upload" /><span class="hidden-xs"> Upload</span></button>
                    <div tabindex="500" class="btn btn-primary btn-file"><font-awesome-icon icon="folder-open" /><input id="avatar" name="file" type="file" ref="file" class="file-loading" @change="changeFile" :accept="acceptFileExtensions"></div>
                    <p></p>
                    <div class="file-preview" v-if="file">
                        <div class="close fileinput-remove" style="cursor: pointer;" @click="removeFile(file)"></div>
                        <div class="file-drop-disabled">
                            <div class="file-preview-thumbnails">
                                <div class="file-preview-frame krajee-default  kv-preview-thumb file-preview-error" data-fileindex="0" data-template="image" style="width: 97%;">
                                    <div class="kv-file-content">
                                        <img :src="imageData" v-if="imageData" class="file-preview-image kv-preview-data" :title="file.name" :alt="file.name" style="width:auto;height:160px;">
                                        <LoadingComp v-else></LoadingComp>
                                    </div>
                                    <div class="file-thumbnail-footer">
                                        <div class="file-footer-caption" :title="file.name">{{ file.name }}<br> <samp>({{ humanSize }})</samp></div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="clearfix"></div>
                        <div class="file-preview-status text-center text-success"></div>
                        <div class="kv-fileinput-error"></div>
                    </div>
                </div>
            </div>
        </form>
    </div>
</template>

<script>
import GenericMixin from '@mixins/generic';
import AvatarMixin from '@mixins/avatar';

const MEGA_SIZE = 1048576;
const KILO_SIZE = 1024;

const CONSTRAINT_FILE = {
    minFileSize: 1, // in KB
    maxFileSize: 5000, // in KB
    minImageWidth: 25,
    minImageHeight: 25,
    maxImageWidth: 150,
    maxImageHeight: 150,
    allowedFileExtensions: ['jpg', 'png', 'gif']
};

export default {
    name: 'avatar-file-upload',

    mixins: [GenericMixin, AvatarMixin],

    computed: {
        displayWidth () {
            return (this.config.width ? this.config.width : '100%');
        },

        displayHeight () {
            return (this.config.height ? this.config.height : '400px');
        },

        awsInputs () {
            return this.config.awsInputs;
        },

        acceptFileExtensions () {
            return CONSTRAINT_FILE.allowedFileExtensions.map((e) => {
                return '.' + e;
            }).join(',');
        },

        humanSize () {
            let size = this.file.size;

            if (size >= MEGA_SIZE) { // 1024 * 1024)
                return (Math.round((size / MEGA_SIZE) * 100 + Number.EPSILON) / 100) + ' MB';
            }

            if (size >= KILO_SIZE) {
                return (Math.round((size / KILO_SIZE) * 100 + Number.EPSILON) / 100) + ' KB';
            }

            if (size > 0) {
                return (Math.round(size * 100 + Number.EPSILON) / 100) + ' B';
            }

            return '0 B';
        },

        isDisabled () {
            return !(!!this.imageData && this.fileErrors.length === 0);
        },
    },

    methods: {
        changeFile () {
            this.imageData = null;
            this.fileErrors = [];
            this.file = this.$refs.file.files[0];
            if (this.file) {
                let reader = new FileReader();
                reader.onload = (e) => {
                    let image = new Image();
                    image.onload = () => {
                        if (image.width < CONSTRAINT_FILE.minImageWidth) {
                            this.fileErrors.push(this.getTrans('file_size_min_width', {'%name%': this.file.name, '%size%': CONSTRAINT_FILE.minImageWidth}));
                        } else if (image.width > CONSTRAINT_FILE.maxImageWidth) {
                            this.fileErrors.push(this.getTrans('file_size_max_width', {'%name%': this.file.name, '%size%': CONSTRAINT_FILE.maxImageWidth}));
                        }

                        if (image.height < CONSTRAINT_FILE.minImageHeight) {
                            this.fileErrors.push(this.getTrans('file_size_min_height', {'%name%': this.file.name, '%size%': CONSTRAINT_FILE.minImageHeight}));
                        } else if (image.height > CONSTRAINT_FILE.maxImageHeight) {
                            this.fileErrors.push(this.getTrans('file_size_max_height', {'%name%': this.file.name, '%size%': CONSTRAINT_FILE.maxImageHeight}));
                        }

                        if ((this.file.size / 1024) > CONSTRAINT_FILE.maxFileSize) {
                            this.fileErrors.push(this.getTrans('file_size_too_big', {'%name%': this.file.name, '%size%': Math.round((this.file.size / KILO_SIZE) * 100 + Number.EPSILON) / 100, '%maxSize%': CONSTRAINT_FILE.maxFileSize}));
                        } else if ((this.file.size / 1024) < CONSTRAINT_FILE.minFileSize) {
                            this.fileErrors.push(this.getTrans('file_size_too_small', {'%name%': this.file.name, '%size%': Math.round((this.file.size / KILO_SIZE) * 100 + Number.EPSILON) / 100, '%minSize%': CONSTRAINT_FILE.minFileSize}));
                        }
                        if (this.fileErrors.length === 0) {
                            this.$refs['aws-input-content-type'][0].value = this.file.type;
                        }
                    };
                    image.src = this.imageData = e.target.result;
                };
                reader.readAsDataURL(this.file);
            }
        },

        removeFile (f) {
            this.file = null;
            this.imageData = null;
            this.fileErrors = [];
        },
    },

    data () {
        return {
            file: null,
            imageData: null,
            fileErrors: []
        };
    }
};
</script>

<style lang="scss" scoped>
@import '~sass/_variables.scss';
@import '~sass/button.scss';
.avatar-file-upload /deep/ {
    .btn-file input[type=file] {
        position: absolute;
        top: 0;
        right: 0;
        min-width: 100%;
        min-height: 100%;
        text-align: right;
        opacity: 0;
        background: none;
        cursor: inherit;
        display: block;
    }

    .file-loading {
        width: 25px;
        height: 25px;
        font-size: 999px;
        color: #fff;
        border: none;
    }
    .kv-avatar {
        .file-input {
            display: table-cell;
            max-width: 300px;
        }
    }
    .krajee-default {
        .file-preview-image {
            image-orientation: from-image;
            vertical-align: middle;
        }
    }

    .file-preview {
        background-color: #fff;
    }
}
</style>



