/* global CryptoJS */
import HTTP from '@/httpapi';
import User from '@api/user';

const GRAVATAR_BASE_URL = 'https://www.gravatar.com/avatar/';

const avatarMixin = {
    props: ['config'],

    computed: {
        gravatarUrl () {
            if (this.user) {
                return GRAVATAR_BASE_URL + CryptoJS.MD5(this.user.email.trim().toLowerCase()).toString();
            }

            return null;
        },

        isPending () {
            return this.config.pending;
        }
    },

    methods: {
        getUserDetails (forceUpdate) {
            let that = this;
            User.myAccount(forceUpdate).then((userDetails) => {
                that.user = userDetails.data.user;
            }).catch((e) => {
                console.error('An error occured:', e.message);
            });
        },

        typeOfAvatar () {
            if (this.user) {
                let type = 'upload';
                if (this.user.avatar.startsWith(GRAVATAR_BASE_URL)) {
                    type = 'gravatar';
                } else if (this.user.avatar.startsWith('https://uploads.asmodee.net/builtin')) {
                    type = 'builtin';
                }

                return type;
            }

            return null;
        },

        currentIs (type) {
            return this.typeOfAvatar() === type;
        }
    },

    mounted () {
        HTTP.ifInitialized(this.getUserDetails);
    },

    data () {
        return {
            user: null
        };
    }
};

export default avatarMixin;



