<template>
    <div :config="config" class="avatar-gravatar" :style="{width: displayWidth, 'min-height': displayHeight}">
        <h1 class="text-center item-title">{{ title }}</h1>
        <div>
            <form class="form-base text-center form-inline" :action="'/' + lang + '/profile/avatar'" method="POST">
                <div class="form-group">
                    <input type="hidden" name="select-gravatar" id="select-gravatar" value="1"/>
                </div>
                <div id="view_gravatar" class="kv-avatar center-block" style="width:200px;">
                    <div class="file-preview">
                        <div class="file-drop-disabled">
                            <div class="file-preview-thumbnails">
                                <div class="file-default-preview">
                                    <img :src="gravatarUrl"/>
                                    <p v-if="currentIs('gravatar') && isPending">{{ getTrans('avatar_pending_approval') }}</p>
                                </div>
                            </div>
                        </div>
                    </div>
                    <br/>
                    <button id="gravatar-select" :class="'btn btn-lg btn-primary btn-block' + (currentIs('gravatar') ? ' disabled' : '')">{{ getTrans('btn_use_gravatar') }}</button>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import GenericMixin from '@mixins/generic';
import AvatarMixin from '@mixins/avatar';

export default {
    name: 'avatar-gravatar',

    mixins: [GenericMixin, AvatarMixin],

    computed: {
        displayWidth () {
            return (this.config.width ? this.config.width : '100%');
        },

        displayHeight () {
            return (this.config.height ? this.config.height : '400px');
        },
    },

    data () {
        return {
        };
    }
};
</script>

<style lang="scss" scoped>
@import '~sass/_variables.scss';
.avatar-handler /deep/ {
}
</style>



