import HTTP from '@/httpapi';
import { cacheReferentiel } from '@lib/cache';

const BuiltinAvatar = {
    filters (locale) {
        return HTTP.http.get(HTTP.convert('/v1/avatar/filters/' + locale), {cache: cacheReferentiel});
    },
    all (filter) {
        return HTTP.http.get(HTTP.convert('/v1/avatars' + (filter ? '/' + filter : '')), {cache: cacheReferentiel});
    },
};

export default BuiltinAvatar;



