<template>
    <div :config="config" class="builtin-avatar-selector" :style="{width: displayWidth, 'min-height': displayHeight}">
        <h1>{{ title }}</h1>
        <template v-if="filters === null"><LoadingComp></LoadingComp></template>
        <template v-else>
            <div>
                <select name="select_filter" class="form-control bs-select" v-model="currentFilter" @change="refreshAvatars()">
                    <option v-for="filter in orderBy(filters, 'name')" :value="filter.id" :key="filter.id">{{ filter.name }}</option>
                </select>
                <panel :config="{width: (this.$mq === 'md' ? 75 : (this.$mq === 'sm' || this.$mq === 'xs' ? 60 : (this.$mq === 'xxs' ? 100 : 77))) + '%', height: displayHeight}">
                    <template slot="body">
                        <template v-if="avatars !== null">
                            <ul class="avatars_list">
                                <li v-for="avatar in avatars" :class="{avatar: true, active: selectedAvatar && selectedAvatar.id === avatar.id}" :key="avatar.id" @click="selectAvatar(avatar)">
                                    <div class="thumb">
                                        <img :src="avatar.img"/>
                                        <span>{{ avatar.name }}</span>
                                    </div>
                                </li>
                            </ul>
                        </template>
                        <template v-else><LoadingComp></LoadingComp></template>
                    </template>
                </panel>
                <panel v-if="typeOfAvatar() === 'builtin' || selectedAvatar !== null" :class="'panelselector' + (this.$mq === 'xxs' ? ' alwaysfoot' : '')" :config="{width: (this.$mq === 'md' ? 23 : (this.$mq === 'sm' || this.$mq === 'xs' ? 38 : (this.$mq === 'xxs' ? 92 : 22))) + '%', height: (this.$mq === 'xxs' ? '120px' : '100%') }">
                    <template slot="body">
                        <form :action="'/' + lang + '/profile/avatar'" method="POST">
                            <input type="hidden" name="select-builtin" id="select-builtin" value="1"/>
                            <input type="hidden" name="selectedAvatar" v-if="selectNewAvatar === true" :value="selectedAvatar.id">
                            <label for="selectedAvatar">{{ selectNewAvatar === true ? getTrans('confirm_use_builtin_avatar') : getTrans('current_avatar') }}</label>
                            <ul class="selector">
                                <li class="avatar"><div class="thumb">
                                        <img v-if="selectedAvatar !== null && selectedAvatar !== undefined" :src="selectedAvatar.img" />
                                        <span>{{ selectedAvatar !== null && selectedAvatar !== undefined ? selectedAvatar.name : '' }}</span>
                                </div></li>
                            </ul>
                            <div class="selector">
                                <button class="btn btn-primary auto" :disabled="selectNewAvatar !== true">{{ getTrans('update') }}</button>
                            </div>
                        </form>
                    </template>
                </panel>
            </div>
        </template>

    </div>
</template>

<script>
import axios from 'axios';
import store from '@/store';
import HTTP from '@/httpapi';
import Games from '@api/games';
import BuiltinAvatar from '@api/builtin_avatar';
import GenericMixin from '@mixins/generic';
import AvatarMixin from '@mixins/avatar';

export default {
    name: 'avatar-builtin-selector',

    mixins: [GenericMixin, AvatarMixin],

    computed: {
        displayWidth () {
            return (this.config.width ? this.config.width : '100%');
        },

        displayHeight () {
            return (this.config.height ? this.config.height : '400px');
        },
    },

    methods: {
        getBuiltins () {
            const that = this;
            axios.all([
                Games.all(),
                BuiltinAvatar.filters(this.lang)
            ]).then(axios.spread(function (games, filters) {
                that.games = games.data.games;
                that.filters = filters.data.filters;
                if (that.selectedAvatar === null || that.selectedAvatar.id === 350) {
                    that.currentFilter = 'fivetribes';
                } else {
                    that.currentFilter = that.config.currentFilter;
                }
                that.refreshAvatars();
            }));
            if (store.getters.baseUser.avatar > 0) {
                this.selectedAvatar = {id: store.getters.baseUser.avatar};
            }
        },

        refreshAvatars () {
            this.avatars = null;
            const that = this;
            BuiltinAvatar.all(this.currentFilter).then(function (result) {
                that.avatars = result.data.avatars;
                if (that.selectedAvatar && !that.selectedAvatar.name) {
                    that.selectedAvatar = that.avatars.filter(function (a) {
                        return a.id === that.selectedAvatar.id;
                    })[0];
                }
            });
        },

        selectAvatar (avatar) {
            this.selectedAvatar = avatar;
            this.selectNewAvatar = true;
        }
    },

    mounted () {
        HTTP.ifInitialized(this.getBuiltins);
    },

    data () {
        return {
            games: null,
            filters: null,
            currentFilter: null,
            avatars: null,
            selectedAvatar: null,
            selectNewAvatar: false
        };
    }
};

</script>

<style lang="scss" scoped>
@import '~sass/_variables.scss';
.builtin-avatar-selector /deep/ {
    h1 {
        color: #b3b3b3;
        font-size: 36px;
        font-weight: 400;
        display: block;
        text-align: center;
    }

    .panel {
        align-content: flex-start;
        display: inline-flex;
        flex-wrap: wrap;

        &.panelselector {
            &.alwaysfoot {
                background-color: #BBBBBB77;
                bottom: 0;
                height: 260px;
                left: 15px;
                padding: 0;
                position: fixed;
                z-index: 4;

                label {
                    background-color: #ffffff;
                }

                .avatar {
                    background-color: #ffffff;
                }

                .panel-body {
                    padding-top: 0;
                }
            }

            .panel-body {
                margin: auto;

                label {
                    margin: 5px auto;
                }

            }
        }

        @include media-breakpoint-down(md) {
            &.panelselector {
                padding-left: 0.5em;
                padding-right: 0.5em;
                min-width: 140px;
            }
        }

        @include media-breakpoint-down(xs) {
            .panel-body {
                margin: auto;
            }
        }
    }

    .avatars_list {
        align-content: flex-start;
        display: inline-flex;
        flex-wrap: wrap;
        list-style: none;
        height: 100%;
        padding: 0;
    }

    .avatar {
        border-radius: 5px;
        box-shadow: -1px 1px 5px 0 #a2958a;
        color: #777;
        display: inline-block;
        font-size: .8em;
        margin: 5px auto;
        max-height: 140px;
        min-height: 108px;
        padding: 5px;
        text-align: center;
        width: 90px;

        &.active {
            background-color: map-get($asnet-colors, dark-purple);
        }
    }

    .avatar .thumb {
        background-color: #fff;
        cursor: pointer;
        display: block;
        text-align: center;
        vertical-align: middle;
        width: 100%;
        height:100%;

        img {
            max-height: 85px;
            max-width: 80px;
        }

        span {
            display: inline-block;
            margin-top:5px;
        }
    }

    .selector {
        list-style: none;
        margin: 5px auto;
        text-align: center;
        width:100%;
        padding: 0;
    }

    @include media-breakpoint-down(sm) {
        .panelselector {
            width: 100%;
        }
    }
}
</style>



